<template>
    <div class="sale-custom clearfix">
        <div class="custom-title food-left left">
            <span>定时上架时段</span>
        </div>
        <div class="custom-content food-right left">
            <div class="tip">
                （注：时段时间不可重合）
            </div>
            <div class="time" v-for="(item,index) in dataArr" :key="index">
              <el-date-picker
                v-model="item.start_time"
                @change="saveTime(index,start_time)"
                type="datetime"
                placeholder="选择日期时间">
              </el-date-picker>

              <el-date-picker
                v-model="item.end_time"
                @change="saveTime(index,end_time)"
                type="datetime"
                placeholder="选择日期时间">
              </el-date-picker>
              <span>                
                <i v-if="index === 0" class="iconfont icon-tianjia blue" @click="addItem"></i>
                <i v-if="index !== 0" class="iconfont icon-shanchu red" @click="deleteItem"></i>
              </span>
            </div>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import { Util } from "@/config/util";

export default {
  props: {
    timeArr: {
      type: Array
    }
  },
  data() {
    var a = {
      dataArr: [
        {
          start_time: null,
          end_time: null
        }
      ],
      start_time: "start_time",
      end_time: "end_time"
    };
    window.a = a;
    return a;
  },
  watch: {
    timeArr: {
      handler: function(newVal, odVal) {
        this.initTime();
      },
      deep: true
    }
  },
  created() {
    this.initTime();
    window.b = moment;
  },
  methods: {
    addItem() {
      let timeObj = { start_time: "", end_time: "" };
      this.dataArr.push(timeObj);
    },
    deleteItem() {
      if (this.dataArr.length > 1) {
        this.dataArr.pop();
      }
    },
    //取数据时间转换
    initTime() {
      if (this.timeArr.length === 0) {
        this.dataArr = [];
        this.dataArr.push({ start_time: "", end_time: "" });
      } else {
        this.dataArr = this.timeArr.map(item => {
          let itemCopy = {};
          for (let items in item) {
            if (item.hasOwnProperty(items)) {
              itemCopy[items] = moment(item[items] * 1000)
                .utc()
                .format();
            }
          }
          return itemCopy;
        });
      }
    },

    //保存时时间转换
    saveTime(index, str) {
      let checkObj = this.dataArr[index];
      let startTime = checkObj.start_time || "";
      let endTime = checkObj.end_time || "";
      let results = this.checkData(startTime, endTime);

      if (!results && str === "start_time") {
        checkObj["start_time"] = "";
      } else if (!results && str === "end_time") {
        checkObj["end_time"] = "";
      }

      let dataTime = this.dataArr.map(item => {
        let itemCopy = {};
        itemCopy["start_time"] = item["start_time"] || "";
        itemCopy["end_time"] = item["end_time"] || "";

        itemCopy["start_time"] = parseInt(
          moment(itemCopy["start_time"]).format("X")
        );
        itemCopy["end_time"] = parseInt(
          moment(itemCopy["end_time"]).format("X")
        );

        return itemCopy;
      });

      this.$emit("time-change", dataTime);
    },
    //开始时间结束时间前后判断
    checkData(startTime, endTime) {
      // debugger;
      if (startTime === "" || endTime === "") {
        return true;
      }

      let start = parseInt(moment(startTime).format("X"));
      let end = parseInt(moment(endTime).format("X"));

      if (start > end) {
        this.$notify({
          title: "警告",
          message: "开始时间不能大于结束时间！",
          type: "warning"
        });
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.custom-content {
  .tip {
    font-size: 14px;
    color: #9b9b9b;
    margin-bottom: 10px;
  }
  .time {
    margin-bottom: 10px;
  }
}
.food-left {
  width: 220px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}

.food-right {
  @include fc(14px, #666);
}

.blue {
  color: #4877e7;
  cursor: pointer;
}
.red {
  color: #e7487e;
  cursor: pointer;
}
</style>




