<template>
  <div id="food-editor">

    <!-- 面包屑 start -->
    <el-breadcrumb separator=">" class="breadcrumb">
      <!-- <el-breadcrumb-item>店铺管理</el-breadcrumb-item> -->
      <el-breadcrumb-item :to="{ path: '/goodmenu/good' }">菜品管理</el-breadcrumb-item>
      <el-breadcrumb-item>新增菜品编辑</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包屑 end -->

    <div class="content-box">

      <!-- 导航 start -->
      <div class="menu-nav">
        <ul class="clearfix">
          <li v-for="(item, index) in menuNavData" 
          :key="index" :class="{active:activeSelect === index}"
          @click="goAnchor(index,item.selector)">{{ item.label }}</li>
        </ul>
      </div>
      <!-- 导航 end -->

      <!-- 基础设置 start -->
      <base-set :foodinfo="foodinfo"
      @foodname-change="getData('foodName', $event)"
      @foodUnit-change="getData('foodUnit', $event)"
      @get-feature="getData('feature', $event)"
      @price-changeT="getData('foodPrice', $event)"
      @img-changeT="getData('foodImgList', $event)"
      @get-composition="getData('composition', $event)"
      @stocknum-change="getData('stockNum', $event)"
      @category-change="getData('categoryId', $event)"
      @category-changeT="clearcategoryId"
      ></base-set>
      <!-- 基础设置 end  -->

      <!-- 口味设置 start -->
      <taste-set :foodinfo="foodinfo"
      @get-attach="getData('food_attach_list', $event)"></taste-set>
      <!-- 口味设置 end  -->

      <!-- 打包设置 start -->
      <pack-set :foodinfo="foodinfo"
      @access-change="getData('accessory', $event)"
      @packnum-change="getData('accessory_num', $event)"
      @remarktext-change="getData('pack_remark', $event)"></pack-set>
      <!-- 打包设置 end  -->

      <!-- 高级设置 start -->
      <high-set :foodinfo="foodinfo"
      @saleway-change="getData('saleWay', $event)"
      @salenum-change="getData('saleNum', $event)"
      @time-changeT="getData('foodSaleTime', $event)"
      @week-change="getData('foodSaleWeek', $event)"
      @salestatus-change="getData('saleOffWay', $event)"
      ></high-set>
      <!-- 高级设置 end  -->
  
      <!-- 促销设置 start -->
      <sale-set></sale-set>
      <!-- 促销设置 end  -->

      <!-- 推广设置 start -->
      <expend-set></expend-set>
      <!-- 推广设置 end  -->

      <div class="btn-content">
        <div class="btn-group clearfix">
          <div class="btn-item left" @click="openPreviewDialog">预览</div>
          <div class="btn-item left" @click="foodSaveDraft">保存草稿</div>
          <div class="btn-item left" @click="foodSave">保存</div>
          <div class="btn-item left" @click="foodSaveSale">保存并上架</div>
          <div class="btn-item left" @click="cancelBtn">取消</div>
        </div>
      </div>

              <!-- 预览弹窗 start -->
        <el-dialog class="preview-dialog-editor" 
        :visible.sync="previewVisible"
        :show-close="false"
        size=""
        top="19%">
          <div class="dialog-content">
            <div class="preview-content">
              <div class="phone-title clearfix">
                <div class="close left">
                  <i class="el-icon-close"></i>
                </div>
                <div class="text left">{{ shopName }}</div>
                <div class="more right">
                  <i class="el-icon-more"></i>
                </div>
              </div>
              <div class="preview-title">
                <div class="back left">
                  <i class="el-icon-arrow-left"></i>
                </div>
                <div class="text left">菜品详情</div>
                <!-- <div class="more right">
                  <i class="iconfont icon-shop-fill"></i>
                </div> -->
              </div>
              <div class="preview-picture">
                <img :src="base_url + '/img_get.php?img=1&imgname='+ previewPicture" alt="菜品图片">
              </div>
              <div class="preview-nameinfo">
                <div class="name-content clearfix">
                  <div class="name left">
                    <div class="title" v-if="foodName === ''">菜品名</div>
                    <div class="title" v-else>{{ foodName }}</div>
                    <div class="detail">
                      <span>月售 100</span>
                      <span>好评率 100%</span>
                    </div>
                  </div>
                  <div class="icon right">
                    <img src="../images/priase.png">
                    <img src="../images/share.png">
                    <img src="../images/heart.png">
                  </div>
                </div>
              <div class="priceicon-content">
                  <div v-if="priceusing === priceType.DISCOUNT" class="price-icon">促销价</div>
                  <div v-if="priceusing === priceType.FESTIVAL" class="price-icon">节日价</div>
                  <div v-if="priceusing === priceType.VIP" class="price-icon">会员价</div>
                </div>
                <div class="price-content clearfix">
                  <div class="price left">
                    <span class="price-first">&yen;{{priceFirst}}</span>
                    <span v-if="priceSecond !== ''" class="price-second">&yen;{{priceSecond}}</span>
                  </div>
                  <div class="cart right">
                    加入购物车
                    <!-- <img src="../view/images/shopcart.png" alt=""> -->
                  </div>
                </div>
              </div> 
              <div class="border"></div>
              <div class="preview-goodinfo">
                <div class="goodinfo-title">商品信息</div>
                <div class="goodinfo-composition">
                  <span>食材：</span>
                  <span v-for="item in composition" :key="item">{{item}} </span>
                </div>
                <div class="goodinfo-feature">
                  <span>特色：</span>
                  <span v-for="item in feature" :key="item">{{item}} </span>
                </div>
                <div class="goodinfo-like">
                  <span>点赞：</span>
                  <span>0</span>
                </div>
              </div>
              <div class="preview-remark">
                <div class="remark-title">
                  商品评价
                </div>
                <div class="remark-border"></div>
                <div class="remark-content">
                  <p>暂无评价</p>
                  <p>赶快去说说你的建议吧！</p>
                </div>
              </div>
            </div>
          </div>
        </el-dialog>
        <!-- 预览弹窗 end -->

    </div>
  </div>
</template>

<script>
import { getFoodEditor, getFoodSn, saveEditor } from "../../api";
import { GoodList } from "../../index";
import {
  GoodType,
  saleStatus,
  priceType,
  FoodType,
  SizeUse,
  errcode
} from "@/config/cfg";
import { Public } from "@/config/public";
import { Util } from "@/config/util";

import BaseSet from "./baseinfoSet";
import TasteSet from "./tasteinfoSet";
import PackSet from "./packinfoSet";
import HighSet from "./highinfoSet";
import SaleSet from "./saleinfoSet";
import ExpendSet from "./expendinfoSet";

export default {
  components: {
    BaseSet,
    TasteSet,
    PackSet,
    HighSet,
    SaleSet,
    ExpendSet
  },
  data() {
    return {
      //基础设置
      foodName: "",
      foodUnit: "",
      feature: null,
      foodPrice: {
        type: 1, // 1 无分量规格价格 2, 有分量规格价格
        using: [1], // 使用哪个价格的位标志(1:原价,2:折扣价,4:会员价,8:节日价)
        price: [
          {
            discount_price: "",
            festival_price: "",
            original_price: "",
            vip_price: ""
          }
        ]
      },
      composition: null,
      stockNum: 0,
      foodImgList: [],
      categoryId: "",
      //高级设置
      saleWay: null,
      saleNum: 1,
      foodSaleTime: null,
      foodSaleWeek: null,
      saleOffWay: null,
      //口味设置
      food_attach_list: null,
      //打包设置
      accessory: "",
      pack_remark: "",
      accessory_num: 0,
      //是否新建
      isNew: true,

      //预览
      shopName: "",
      base_url: base_url,
      priceusing: "",
      priceFirst: "",
      priceSecond: "",
      previewVisible: false,
      priceType: priceType,

      menuNavData: [
        {
          label: "基础设置",
          selector: "#base-set"
        },
        {
          label: "口味设置",
          selector: "#taste-set"
        },
        {
          label: "打包设置",
          selector: "#pack-set"
        },
        {
          label: "高级设置",
          selector: "#high-set"
        },
        {
          label: "促销设置",
          selector: "#sale-set"
        },
        {
          label: "推广设置",
          selector: "#expend-set"
        }
      ],
      activeSelect: 0,
      foodId: null,
      foodinfo: {
        food_id: "",
        shop_id: "",
        food_name: "",
        food_unit: "",
        category: "",
        feature: [],
        composition: [], //食材
        food_price: {
          type: 1, // 1 无分量规格价格 2, 有分量规格价格
          using: [1], // 使用哪个价格的位标志(1:原价,2:折扣价,4:会员价,8:节日价)
          price: [
            {
              discount_price: "",
              festival_price: "",
              original_price: "",
              vip_price: ""
            }
          ]
        },
        food_sale_time: [
          {
            start_time: null,
            end_time: null
          }
        ],
        food_attach_list: [
          {
            title: null,
            list: null
          }
        ]
      }
    };
    // window.a = a;
    // return a;
  },
  computed: {
    previewPicture() {
      return (this.foodImgList || [])[0] || "";
    }
  },
  created() {
    this.getFoodinfo();
    this.getfoodSnn();
  },
  methods: {
    //锚跳转
    goAnchor(index, selector) {
      this.activeSelect = index;
      GoodList.goAnchor(this.$el, selector);
    },
    //拉取编辑信息
    getFoodinfo() {
      this.foodId = this.$route.query.foodId;

      if (!GoodList.isEmpty(this.foodId)) {
        this.isNew = false;

        getFoodEditor(this.foodId, resp => {
          if (resp.ret === 0) {
            this.foodinfo = resp.data.info || {};

            //基础设置
            this.foodName = this.foodinfo.food_name;
            this.foodUnit = this.foodinfo.food_unit || "个";
            this.feature = this.foodinfo.feature;
            this.foodPrice = this.foodinfo.food_price;
            this.composition = this.foodinfo.composition;
            this.stockNum = this.foodinfo.stock_num_day;
            this.foodImgList = this.foodinfo.food_img_list;
            this.categoryId = this.foodinfo.category_id;

            //高级设置
            this.saleWay = this.foodinfo.sale_way;
            this.saleNum = this.foodinfo.sale_num;
            this.foodSaleTime = this.foodinfo.food_sale_time;
            this.foodSaleWeek = this.foodinfo.food_sale_week;
            this.saleOffWay = this.foodinfo.sale_off_way;

            //口味设置
            this.food_attach_list = this.foodinfo.food_attach_list;

            //打包设置
            this.accessory = this.foodinfo.accessory;
            this.pack_remark = this.foodinfo.pack_remark;
            this.accessory_num = this.foodinfo.accessory_num;
          } else {
            console.warn("获取菜品详情失败! ");
          }
        });
      }
    },

    //得到编号
    getfoodSnn() {
      let data = {
        genid: 1,
        type: "food"
      };

      if (this.isNew) {
        getFoodSn(data, resp => {
          if (resp.ret === 0) {
            this.foodinfo.food_id = resp.data.id;
            this.foodinfo.shop_id = Public.getShopid();
          } else {
            console.warn("获取编号错误! ");
          }
        });
      }
    },
    //获取子组件信息
    getData(name, value) {
      this[name] = value;
    },
    //获取保存的信息
    getSaveData() {
      let data = {
        save: 1,
        food_id: this.foodinfo.food_id,
        food_name: this.foodName,
        category_id: this.categoryId,
        food_price: this.foodPrice,
        stock_num_day: this.stockNum,
        food_img_list: this.foodImgList,
        feature: this.feature,
        composition: this.composition,
        food_unit: this.foodUnit,
        sale_way: this.saleWay,
        sale_num: this.saleNum,
        sale_off_way: this.saleOffWay,
        //打包
        accessory_num: this.accessory_num,
        pack_remark: this.pack_remark,
        accessory: this.accessory,
        //口味
        food_attach_list: this.food_attach_list,
        type: GoodType.DISH
      };

      if (this.saleOffWay === saleStatus.CUSTOME) {
        data["food_sale_time"] = this.foodSaleTime;
      } else if (this.saleOffWay === saleStatus.WKKE) {
        data["food_sale_week"] = this.foodSaleWeek;
      }

      if (this.isNew) {
        data["entry_type"] = 1;
      }

      //处理价格
      if (Array.isArray(data.food_price.using)) {
        let use = 0;
        data.food_price.using.forEach(item => {
          use = use | item;
        });
        data.food_price.using = use;
      }

      return data;
    },
    //取消
    cancelBtn() {
      this.$router.go(-1);
    },
    //预览
    openPreviewDialog() {
      this.shopName = GoodList.getShopname();

      let arr = [priceType.DISCOUNT, priceType.FESTIVAL, priceType.VIP];
      let using = this.foodPrice.using;
      let type = this.foodPrice.type;

      arr.forEach(item => {
        if ((using & item) !== 0) {
          this.priceusing = item;
        }
      });
      if (type === FoodType.YES) {
        //无分量
        if (this.priceusing !== "") {
          switch (this.priceusing) {
            case priceType.DISCOUNT:
              this.priceFirst = ((this.foodPrice.price || [])[0] || {}
              ).discount_price;
              this.priceSecond = ((this.foodPrice.price || [])[0] || {}
              ).original_price;
              break;
            case priceType.FESTIVAL:
              this.priceFirst = ((this.foodPrice.price || [])[0] || {}
              ).festival_price;
              this.priceSecond = ((this.foodPrice.price || [])[0] || {}
              ).original_price;
              break;
            case priceType.VIP:
              this.priceFirst = ((this.foodPrice.price || [])[0] || {}
              ).vip_price;
              this.priceSecond = ((this.foodPrice.price || [])[0] || {}
              ).original_price;
              break;
          }
        } else {
          this.priceFirst = ((this.foodPrice.price || [])[0] || {}
          ).original_price;
        }
      } else if (type === FoodType.NO) {
        //有分量
        let sizePrice = {};

        (this.foodPrice.price || []).forEach(item => {
          if (item.is_use === SizeUse.YES) {
            sizePrice = item;
          }

          return;
        });

        if (this.priceusing !== "") {
          switch (this.priceusing) {
            case priceType.DISCOUNT:
              this.priceFirst = sizePrice.discount_price;
              this.priceSecond = sizePrice.original_price;
              break;
            case priceType.FESTIVAL:
              this.priceFirst = sizePrice.festival_price;
              this.priceSecond = sizePrice.original_price;
              break;
            case priceType.VIP:
              this.priceFirst = sizePrice.vip_price;
              this.priceSecond = sizePrice.original_price;
              break;
          }
        } else {
          this.priceFirst = sizePrice.original_price;
        }
      }
      this.previewVisible = true;
    },
    //菜品保存
    foodSave() {
      //必填项检验
      if (this.check()) {
        return;
      }

      let data = this.getSaveData();
      data["sale_off"] = 1;
      data["is_draft"] = 0;

      saveEditor(data, this.saveHander);
    },
    //菜品草稿保存
    foodSaveDraft() {
      //必填项检验
      if (this.check()) {
        return;
      }

      let data = this.getSaveData();
      data["is_draft"] = 1;

      saveEditor(data, resp => {
        if (resp.ret === 0) {
          console.log("保存成功！");
          this.$router.push({ path: "/good/draft" });
        } else if (resp.ret === errcode.FOOD_EXIST) {
          this.$notify({
            title: "警告",
            message: "保存失败，菜品名称已存在",
            type: "warning"
          });
          console.warn("保存失败！ ");
        } else {
          console.warn("保存失败！");
        }
      });
    },
    //菜品保存并上架
    foodSaveSale() {
      //必填项检验
      if (this.check()) {
        return;
      }

      let data = this.getSaveData();
      data["sale_off"] = 0;
      data["is_draft"] = 0;

      saveEditor(data, this.saveHander);
    },

    saveHander(resp) {
      if (resp.ret === 0) {
        console.log("保存成功！");
        //保存后跳转至所属分类列表
        this.$store.commit("CHANGE_ID", this.categoryId);
        this.$router.push({ path: "/goodmenu/good" });
      } else if (resp.ret === errcode.FOOD_EXIST) {
        this.$notify({
          title: "警告",
          message: "保存失败，菜品名称已存在",
          type: "warning"
        });
        console.warn("保存失败！ ");
      } else {
        console.warn("保存失败！");
      }
    },
    clearcategoryId() {
      this.categoryId = "";
    },
    //必填项检验
    check() {
      //name
      if (this.foodName === "" || GoodList.isEmpty(this.foodName)) {
        this.$notify({
          title: "警告",
          message: "菜品名称为必填",
          type: "warning"
        });
        return true;
      }
      //商品分类
      if (this.categoryId === "" || GoodList.isEmpty(this.categoryId)) {
        this.$notify({
          title: "警告",
          message: "商品分类为必填",
          type: "warning"
        });
        return true;
      }
      //规格
      if (this.foodUnit === "" || GoodList.isEmpty(this.foodUnit)) {
        this.$notify({
          title: "警告",
          message: "商品规格为必填",
          type: "warning"
        });
        return true;
      }
      //价格
      if (this.checkPrice()) {
        return true;
      }
      //自定义时间段
      if (this.saleOffWay === saleStatus.CUSTOME && !this.checksaleTime()) {
        this.$notify({
          title: "警告",
          message: "不能存在重复时间段",
          type: "warning"
        });
        return true;
      }

      //选择限量时日备货量数
      if (this.stockNum === "") {
        this.$notify({
          title: "警告",
          message: "请输入日备货量数",
          type: "warning"
        });
        return true;
      }
    },
    checkPrice() {
      let selectPrice = [];
      let arr = [
        priceType.ORIGINAL,
        priceType.DISCOUNT,
        priceType.FESTIVAL,
        priceType.VIP
      ];
      let using = this.foodPrice.using || 1;

      let type = this.foodPrice.type;
      let price = this.foodPrice.price || [];
      let checkBoolean = false; //价格必填项判断
      let sizeSelectBoolean = false; //有分量时至少选择一个价格，

      arr.forEach(item => {
        if ((using & item) !== 0) {
          selectPrice.push(item);
        }
      });

      switch (type) {
        //大，中，小分量中的价格
        case FoodType.NO:
          price.forEach(item => {
            if (item.is_use === SizeUse.YES) {
              sizeSelectBoolean = true;
              selectPrice.forEach(items => {
                if (
                  Util.isEmpty(item[priceType.toStringEn(items)]) ||
                  item[priceType.toStringEn(items)] === ""
                ) {
                  checkBoolean = true;
                  return;
                }
              });
            }
          });
          break;

        case FoodType.YES:
          selectPrice.forEach(item => {
            if (
              Util.isEmpty((price[0] || {})[priceType.toStringEn(item)]) ||
              (price[0] || {})[priceType.toStringEn(item)] === ""
            ) {
              checkBoolean = true;
              return;
            }
          });
          break;
      }

      //至少有一个分量判断
      if (checkBoolean) {
        this.$notify({
          title: "警告",
          message: "选择的价格为必填",
          type: "warning"
        });
      }

      if (type === FoodType.NO && !checkBoolean && !sizeSelectBoolean) {
        this.$notify({
          title: "警告",
          message: "至少选中一个分量大小",
          type: "warning"
        });
        checkBoolean = true;
      }
      return checkBoolean;
    },
    checksaleTime() {
      let timeBoolean = true;
      let startTimeArr = [];
      let endTimeArr = [];

      this.foodSaleTime.forEach(item => {
        startTimeArr.push(item.start_time);
        endTimeArr.push(item.end_time);
      });

      let begin = startTimeArr.sort();
      let over = endTimeArr.sort();

      for (var k = 1; k < begin.length; k++) {
        if (begin[k] <= over[k - 1]) {
          timeBoolean = false;
        }
      }

      return timeBoolean;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.breadcrumb {
  margin-bottom: 14px;
}

.content-box {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.menu-nav {
  @include fc(14px, #333333);
  background-color: #fff;
  height: 40px;
  line-height: 36px;

  ul {
    li {
      float: left;
      cursor: pointer;
    }
    li:first-child {
      margin-left: 14px;
    }
    li {
      margin-left: 80px;
    }
    li.active {
      border-bottom: 4px solid #4877e7;
    }
  }
}

.btn-content {
  background-color: #f6f8fc;
  position: relative;
  height: 80px; // margin-bottom: 50px;
  .btn-group {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);

    .btn-item {
      @include wh(100px, 40px);
      line-height: 40px;
      text-align: center;
      border: 1px solid #4877e7;
      cursor: pointer;
      border-radius: 4px;
      color: #4877e7;
      margin-right: 10px;
    }

    .btn-item:last-child {
      margin-right: 10px;
    }
  }
}
.preview-dialog-editor {
  .dialog-content {
    @include wh(300px, 606px);
    background-image: url(../images/telephone.png);
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;

    .preview-content {
      @include wh(259px, 461px);
      background-color: rgb(241, 241, 241);
      position: absolute;
      left: 20px;
      top: 73px;
    }
  }
}
.preview-content {
  .phone-title {
    @include wh(100%, 30px);
    color: #fff;
    background-color: rgb(46, 46, 46);
    padding: 0 14px;
    line-height: 30px;
    .text {
      margin: 0 18px;
      font-size: 16px;
    }
    .more .el-icon-more {
      transform: rotate(90deg);
    }
  }
  .preview-title {
    @include wh(100%, 27px);
    color: #393939;
    background-color: #fff;
    line-height: 27px;
    padding: 0 14px;
    box-shadow: 0 1px 1px 0 #7d3901;

    .text {
      width: 163px;
      margin: 0 18px;
      text-align: center;
    }
  }
  .preview-picture {
    background-color: #fff;
    @include wh(100%, 80px);
    img {
      @include wh(100%, 80px);
    }
  }
  .preview-nameinfo {
    background-color: #fff;
    // @include wh(100%, 80px);
    padding: 5px 14px 10px 14px;
    .name-content {
      .name {
        .title {
          font-weight: bold;
        }
        .detail {
          @include fc(12px, #8a8a8a);
        }
      }
      .icon {
        img {
          @include wh(20px, 20px);
          padding-right: 5px;
        }
        img:last-child {
          padding-right: 0px;
        }
      }
    }
    .priceicon-content {
      height: 26px;
      padding-top: 5px;
    }
    .price-content {
      .price {
        .price-first {
          color: #ff3637;
          font-weight: bold;
          font-size: 16px;
        }
        .price-second {
          color: #8a8a8a;
          font-size: 12px;
        }
      }
      .cart {
        @include fc(12px, #fff);
        @include wh(92px, 20px);
        line-height: 20px;
        padding-left: 24px;
        background-image: url(../images/shopcart.png);
        background-repeat: no-repeat;
        background-size: contain;
        img {
          @include wh(65px, 16px);
        }
      }
    }
  }
  .preview-goodinfo {
    background-color: #fff;
    // @include wh(100%, 100px);
    @include fc(12px, #393939);
    padding: 14px;

    .goodinfo-title {
      font-size: 14px;
      margin-bottom: 5px;
    }
  }
  .preview-remark {
    background-color: #fff;
    margin-top: 10px;
    padding: 0 10px;

    .remark-border {
      border-bottom: 1px solid #ccc;
    }
    .remark-title {
      height: 27px;
      line-height: 27px;
      font-weight: bold;
      padding: 0 4px;
    }
    .remark-content {
      text-align: center;
      padding: 17px 0 20px 0;
      color: #8a8a8a;
    }
  }
  .border {
    margin: 0 10px;
    border-bottom: 1px solid #ccc;
  }
  .price-icon {
    border: 1px solid #ff6f06;
    background-color: rgb(255, 247, 240);
    display: inline-block;
    font-size: 12px;
    padding: 0 2px;
    color: #ff6f06;
    border-radius: 2px;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#food-editor {
  .el-breadcrumb {
    @include fc(12px, #333333);
  }

  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner {
    color: #4877e7;
  }

  .el-breadcrumb__item__inner:hover {
    color: #4877e7;
  }

  .preview-dialog-editor {
    .el-dialog {
      background: none;
      box-shadow: none;

      .el-dialog__body {
        padding: 0 30px;
      }
    }
  }
}
</style>






